import React from 'react'
import Layout from '../components/layout'
import Masonry from 'react-masonry-component'

import './style/venue.css'

import entrance from '../assets/images/venetian/entrance.jpg'
import fountain from '../assets/images/venetian/fountain.jpg'
import gbr1 from '../assets/images/venetian/grand-ballroom-1.jpg'
import gbr2 from '../assets/images/venetian/grand-ballroom-2.jpg'
import gbr3 from '../assets/images/venetian/grand-ballroom-3.jpg'
import gbr4 from '../assets/images/venetian/grand-ballroom-4.jpg'
import hall from '../assets/images/venetian/hall.jpg'
import palazzo_lobby from '../assets/images/venetian/palazzo-lobby.jpg'
import plate_setting from '../assets/images/venetian/plate-setting.jpg'

const IMAGES = [
    entrance,
    fountain,
    gbr1,
    gbr2,
    gbr3,
    gbr4,
    hall,
    palazzo_lobby,
    plate_setting,
]

class Venue extends React.Component {
    constructor(context, props) {
        super(context, props)
        this.state = {loaded: false}
        this.show = this.show.bind(this)
    }
    show() {
        if (!this.state.loaded) {
            this.setState({loaded: true})
        }
    }
    render() {
        const style = this.state.loaded ? {} : {'display': 'none'}
        return (
            <Layout>
                <div className='venue'>
                    <div className='venue-hero'>
                        <div className='venue-hero-img'>
                            <span className='venue-hero-text'>
                                The Venetian
                            </span>
                        </div>
                    </div>

                    <div className='venue-content'>
                        <Masonry style={style} onImagesLoaded={this.show}>
                            {IMAGES.map((img, i) => (
                                <div key={i} className='venetian-image'>
                                    <img src={img} alt='venetian' />
                                </div>
                            ))}
                        </Masonry>
                        {!this.state.loaded && (
                            <div className='bouncing-loader'>
                                <div />
                                <div />
                                <div />
                            </div>
                        )}
                    </div>
                </div>
            </Layout>
        )
    }
}

export default Venue
